.App{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.button-static{
  background: #F3BD00;
  color: white;
  font-size: 2rem;
  
  width: auto;
  height: 3rem;
  border: none;
  font-family: "poppins",sans-serif;
  padding: 0px 10px;
  border-radius: 1rem;
  font-size: 1.2rem;
  cursor: pointer;
  transition: 0.3s;
}
.button-static:hover{

  background: white;
  border: 1px solid #F3BD00;
  color: #2f2d2e;
}
.button{
  background: white;
  border: 1px solid #F3BD00;
  font-family: "poppins",sans-serif;
  padding: 0px 10px;
  margin :0.8rem;
  border-radius: 1rem;
  cursor: pointer;
  transition: 0.3s;

}

.button:hover{
  background: #F3BD00;
  color: white;
}
