.navbar {
  background: #fff;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  
}

.nav-center {
  width: 90vw;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#logo{
  height: 80px;
}
.nav-header img {
  
  margin-right: 10px;
}

.nav-toggle {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 2rem;
  display: none;
}

.nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  list-style: none;
  color: #287a57;
  text-decoration: none;
}

.nav-links a {
  color: #333;
  text-transform: capitalize;
  margin: 0 1rem;
  transition: all 0.3s linear;
  padding: 0.5rem;
  border-radius: 0.5rem;
  text-decoration: none;
}

.nav-links a:hover {
  background: #333;
  color: #fff;
}

.nav-links a:visited{
  text-decoration: none;
  list-style: none;
}

.show-nav {
  display: flex;
  flex-direction: column;
  text-align: center;
  position: absolute;
  top: 80px;
  right: 0;
  background: #fff;
  width: 100%;
  border-radius: 0 0 0.5rem 0.5rem;
}

@media screen and (max-width: 768px) {
  .nav-center {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .nav-links {
    display: none;
  }

  .nav-toggle {
    display: block;
  }
  .nav-header{
    width: 100%;
  }
  .show-nav {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 80px;
    right: 0;
    background: #fff;
    width: 100%;
    border-radius: 0 0 0.5rem 0.5rem;
  }

  .show-nav li {
    margin: 0.5rem 0;
  }

 
}

